'use strict';

var URL = global.window ? window.URL : require('url').URL;

var urlRegex = require('url-regex')({
  exact: true
});

var REGEX_HTTP_PROTOCOL = /^https?:\/\//i;

module.exports = function (url) {
  try {
    var _ref = new URL(url),
        href = _ref.href;

    return REGEX_HTTP_PROTOCOL.test(href) && urlRegex.test(href);
  } catch (err) {
    return false;
  }
};